html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h2 {
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 0;
}

h3 {
  font-size: 2.6rem;
  font-family: "Addington CF";
  font-weight: 600;
}

hr {
  width: 7.5rem;
  color: #45b8ac;
  height: 0.2rem;
  background-color: #45b8ac;
  display: inline-block;
  border: solid 0.1rem #45b8ac;
}

ul {
  display: contents;
}

li {
  text-decoration: none;
  list-style: none;
}

.form {
  background-color: hsl(0, 0%, 99%);
  width: 50rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto 10rem;
  box-shadow: #d4f7f4 0.5rem 0.5rem 1rem;
}
.form .login-button {
  background-color: #0473ba;
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  margin: 2rem 0 2rem 1rem;
}
.form ::placeholder {
  color: #0473ba;
  font-size: 1.4rem;
  font-weight: 600;
}
.form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 5rem 0;
}
.form form fieldset {
  border: none;
}
.form form .error {
  display: flex;
  background-color: red;
  height: 3rem;
  color: #ffffff;
  align-items: center;
  justify-content: center;
}
.form form input {
  display: block;
  width: 30rem;
  margin: 1rem;
  height: 3rem;
  border: none;
  border-bottom: solid #0473ba 0.1rem;
  padding-left: 1rem;
  background-color: hsl(0, 0%, 99%);
}
.form form label,
.form form a,
.form form p {
  margin: 1rem;
  font-size: 1.4rem;
  font-weight: 600;
  color: #0473ba;
}
.form form p {
  display: inline-flex;
  margin: 0.5rem;
}
.form form a {
  margin: 0.5rem;
  color: #45b8ac;
  text-decoration: none;
}
.form form a :visited {
  color: #45b8ac;
}
.form form legend {
  font-size: 2rem;
  font-weight: 600;
  padding: 1rem;
  margin: 1rem 0;
  color: #134c8f;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 767px;
  overflow: hidden;
}

.nav-bar {
  display: flex;
  margin: 4.5rem;
}
.nav-bar .logo {
  width: 35%;
  margin: 0 7%;
}
.nav-bar a {
  font-family: "Raleway", sans-serif;
  font-size: 1.4rem;
  font-weight: 600;
  color: #134c8f;
  text-decoration: none;
}
.nav-bar .nav-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding: 0 2rem;
}
.nav-bar .nav-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15rem;
  margin: 0 7%;
}

.saludo {
  display: flex;
  justify-content: start;
  color: #134c8f;
}
.saludo span {
  color: #45b8ac;
}

header h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}
header h1 pre {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.categoria-productos {
  display: block;
}
.categoria-productos h2 {
  display: flex;
  place-content: center;
  color: #0473ba;
}
.categoria-productos form {
  display: flex;
}
.categoria-productos form .boton-agregar {
  margin-left: 2rem;
}
.categoria-productos table {
  display: block;
  margin: 2rem auto;
}
.categoria-productos table tbody {
  display: block;
  margin: 0 auto;
  width: max-content;
}
.categoria-productos table tbody th {
  font-size: 1.4rem;
  text-align: center;
  padding: 1rem;
  color: #0473ba;
  border: solid 0.1rem #0473ba;
}
.categoria-productos table tbody td {
  font-size: 1.2rem;
  text-align: left;
  padding: 1rem;
  border: solid 0.1rem #45b8ac;
  font-weight: 500;
}
.categoria-productos table tbody td .cantidad {
  width: 5rem;
  text-align: center;
}

.collections {
  display: block;
}
.collections .pioneering-collection {
  background-image: url("/Ecommerce/build/img/images/coleccion-pioneros-mobile.webp");
  background-size: cover;
  max-width: 48rem;
  width: 100%;
  height: 29rem;
  margin-bottom: 4rem;
}
.collections .pioneering-collection h2 {
  font-size: 2.8rem;
  color: #ffffff;
  margin: 2rem 1.7rem;
  padding-top: 12rem;
}
.collections .pioneering-collection .green-button {
  display: flex;
  margin: 0 1.7rem;
}
.collections .change-your-way-collection {
  background-image: url("/Ecommerce/build/img/images/coleccion-change-your-way-mobile.webp");
  background-size: contain;
  max-width: 48rem;
  width: 100%;
  height: 29rem;
  margin-bottom: 4rem;
  background-repeat: round;
}
.collections .change-your-way-collection h2 {
  font-size: 2.8rem;
  color: #134c8f;
  margin: 4rem 0 0 18.5rem;
  padding-top: 10rem;
}
.collections .change-your-way-collection .green-button {
  display: flex;
  margin: 1.5rem 0 0 18.5rem;
}

.green-button {
  background-color: #45b8ac;
  font-family: "Raleway", sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
  border: none;
  border-radius: 2rem;
}
.green-button a {
  text-decoration: none;
  color: #ffffff;
  padding: 0.5rem 1.5rem;
}

.categories {
  color: #ffffff;
  background-color: #134c8f;
  margin: 0;
}
.categories .green-button {
  margin: 5rem 32%;
  line-height: 3rem;
}

.categories-container {
  display: block;
  grid-gap: 2rem;
  margin: 5rem 5rem 0 5rem;
  max-width: 48rem;
  overflow: hidden;
}
.categories-container li {
  max-height: 70rem;
  margin-right: 5rem;
}

.imagen-categorias img {
  position: relative;
  width: 20rem;
  height: 100%;
  max-height: 40rem;
  z-index: 1;
}

.barra-categorias-imagen {
  height: 15rem;
}

.barra-categorias-imagen img {
  bottom: 21rem;
  max-height: 40rem;
  position: relative;
  z-index: 0;
}

.texto-categorias {
  position: relative;
  bottom: 42rem;
  left: 6rem;
}
.texto-categorias p {
  line-height: 17px;
  letter-spacing: 1px;
  font-size: 1.2rem;
}

.top-sales {
  color: #134c8f;
}
.top-sales .top-sales-container-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 2rem;
  margin: 5rem 0;
  padding: 0;
}
.top-sales .top-sales-container-grid li {
  position: relative;
}
.top-sales .top-sales-container-grid li .descuento {
  display: inline-block;
  width: 6.5rem;
  padding: 0.8rem;
  padding-left: 1.5rem;
  background-color: #ff9c00;
  color: #ffffff;
  border-radius: 0 2rem 2rem 0;
  font-weight: 600;
  position: absolute;
  bottom: 21rem;
  left: 0;
}
.top-sales .top-sales-container-grid img {
  width: 20rem;
}
.top-sales .top-sales-container-grid p {
  font-size: 1.6rem;
  font-weight: 500;
  margin: 0 0 0.5rem 0;
}
.top-sales .top-sales-container-grid .precio-naranja {
  color: #ff9c00;
  display: inline-block;
  margin-right: 1rem;
}
.top-sales .top-sales-container-grid .precio-tachado {
  color: #cecece;
  text-decoration: line-through;
  display: inline-block;
}
.top-sales .top-sales-container-grid .icono-like-blanco img {
  width: 4.4rem;
  position: absolute;
  bottom: 21rem;
  left: 15.4rem;
  z-index: 1;
}
.top-sales .flechas-carrusel {
  display: flex;
  justify-content: end;
  gap: 1rem;
}
.top-sales .flechas-carrusel .flecha-izquierda,
.top-sales .flechas-carrusel .flecha-derecha {
  display: inline-flex;
}

.titulo-barra-bloque {
  display: block;
  margin-left: 36%;
}
.titulo-barra-bloque h2 {
  padding-top: 6rem;
}

.solidarity-action {
  color: #134c8f;
  margin-top: 5rem;
}
.solidarity-action h2 {
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  position: relative;
}
.solidarity-action h2 span {
  position: absolute;
  bottom: -5.5rem;
  left: 0;
}
.solidarity-action h2 span img {
  scale: 0.75;
}

.solidarity-action-text {
  margin-top: 7rem;
}
.solidarity-action-text .solidarity-action-container {
  display: flex;
  align-content: center;
}
.solidarity-action-text .solidarity-action-container .solidarity-action-text-bar {
  background: #45b8ac;
  height: 15rem;
  width: 0.5rem;
  display: inline-block;
}
.solidarity-action-text .solidarity-action-container .solidarity-action-paragraph {
  display: inline-block;
  max-width: 55rem;
  margin: auto 0 auto 2rem;
  width: 97%;
  padding-left: 1rem;
}
.solidarity-action-text .solidarity-action-container .solidarity-action-paragraph p {
  margin: auto;
  font-size: 1.6rem;
  font-weight: 500;
}
.solidarity-action-text .solidarity-action-container .solidarity-action-paragraph p:last-of-type {
  margin-top: 1rem;
}

.solidarity-action-logo {
  display: none;
}
.solidarity-action-logo img {
  width: 40%;
  position: relative;
  left: 700px;
  bottom: 100px;
}

.solidarity-action-productos {
  margin-bottom: 6rem;
}
.solidarity-action-productos ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding: 0;
}
.solidarity-action-productos img {
  width: 40rem;
}
.solidarity-action-productos .icono-like-blanco img {
  width: 4.4rem;
  position: relative;
  bottom: 38rem;
  left: 34rem;
  z-index: 1;
}
.solidarity-action-productos p {
  font-size: 1.6rem;
  font-weight: 500;
  margin: 0;
  margin: 0 0 0.5rem;
}

.precio,
.precio-naranja,
.precio-tachado {
  font-size: 1.8rem;
  font-weight: 500;
  margin: 0;
  color: #0473ba;
}

.newsletter {
  background: linear-gradient(98deg, rgb(4, 115, 186) 0%, rgb(69, 184, 172) 100%);
}
.newsletter .newsletter-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 10rem;
  align-items: center;
  gap: 5rem;
  height: 25rem;
}
.newsletter h3 {
  display: grid;
  margin: 0;
  color: #ffffff;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}
.newsletter p {
  margin: 1rem 0 0 0;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 500;
}

.formulario input {
  color: #0473ba;
  height: 3rem;
  border-radius: 0.5rem;
  border: none;
  box-shadow: 0 0 7px 0 rgba(255, 255, 255, 0.5);
  width: 35rem;
  padding-left: 2rem;
  font-size: 1.2rem;
  font-weight: 600;
  display: block;
  margin-bottom: 2rem;
}

.orange-button {
  background-color: #ff9c00;
  color: #ffffff;
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
  border: none;
  border-radius: 2rem;
  display: flex;
  margin-top: 2rem;
  padding: 0.5rem 2rem;
}

.footer {
  height: 43.5rem;
  background: linear-gradient(180deg, rgb(25, 90, 150) 0%, rgb(31, 136, 166) 100%);
}
.footer p {
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 400;
}
.footer h4 {
  color: #ffffff;
  font-size: 2.6rem;
  font-weight: 500;
  font-family: "Addington CF";
  margin: 3rem 0 1rem 0;
}
.footer hr {
  width: 3rem;
  height: 0.2rem;
  border: none;
}
.footer .logo-universae-footer img {
  scale: 1.25;
}
.footer .contenedor-footer-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-gap: 6rem;
  padding-top: 5rem;
  margin-bottom: 5rem;
}
.footer .contenedor-iconos {
  display: block;
  margin: auto 0;
}
.footer .contenedor-iconos .logo-social-media-footer {
  margin-top: 5rem;
  margin-right: 15rem;
  display: flex;
  justify-content: space-between;
}
.footer .barra-blanca hr {
  color: #ffffff;
  width: 100%;
  background-color: #ffffff;
  border: none;
  height: 0.2rem;
}
.footer .barra-blanca p {
  margin: 3rem auto;
  text-align: center;
}

@media (min-width: 768px) {
  .container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    max-width: 1023px;
  }
  h2 {
    font-size: 2.6rem;
    font-weight: 600;
    margin-bottom: 0;
  }
  h3 {
    font-size: 2rem;
    font-family: "Addington CF";
    font-weight: 600;
  }
  hr {
    width: 5rem;
  }
  .nav-bar {
    margin: 4.5rem 0;
  }
  .nav-bar .nav-links {
    padding: 0;
    width: 80%;
    gap: 2rem;
  }
  .nav-bar .nav-icons {
    margin: 0 0 0 1rem;
  }
  .burger_menu img {
    display: none;
  }
  .collections {
    display: block;
  }
  .collections .pioneering-collection {
    background-image: url("/Ecommerce/build/img/images/coleccion-pioneros-desktop.webp");
    display: grid;
    align-items: center;
    max-width: 86rem;
    background-size: cover;
    background-repeat: no-repeat;
    height: 40rem;
  }
  .collections .pioneering-collection .green-button {
    width: max-content;
    margin: 0 0 12rem 50%;
  }
  .collections .pioneering-collection h2 {
    font-size: 3.3rem;
    color: #ffffff;
    margin: 0 50%;
  }
  .collections .change-your-way-collection {
    background-image: url("/Ecommerce/build/img/images/coleccion-change-your-way-desktop.webp");
    display: grid;
    align-items: center;
    max-width: 86rem;
    background-size: cover;
    background-repeat: no-repeat;
    height: 40rem;
  }
  .collections .change-your-way-collection .green-button {
    width: max-content;
    margin: 0 0 12rem 50%;
  }
  .collections .change-your-way-collection h2 {
    font-size: 3.3rem;
    color: #134c8f;
    margin: 0 50%;
  }
  .top-sales .top-sales-container-grid li {
    position: relative;
  }
  .top-sales .top-sales-container-grid li .descuento {
    position: absolute;
    bottom: 39rem;
    left: 0;
  }
  .top-sales .top-sales-container-grid img {
    width: 40rem;
  }
  .top-sales .top-sales-container-grid .icono-like-blanco img {
    position: absolute;
    bottom: 39rem;
    left: 34.4rem;
  }
  .categories {
    color: #ffffff;
    background-color: #134c8f;
    margin: 0;
  }
  .categories .green-button {
    width: max-content;
    margin: 9rem 0 5rem 50%;
    line-height: 3rem;
  }
  .categories-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    margin: 5rem 5rem 0 5rem;
    max-width: 80rem;
    overflow: hidden;
  }
  .categories-container li {
    max-height: 70rem;
    margin-right: 5rem;
  }
  .imagen-categorias img {
    position: relative;
